import '@styles/components/Maps.scss';

function KoreaMap({selectedArea}: {selectedArea: string}) {
  return (
    <svg className='map_svg' style={{background:'transparent', overflow:'visible'}} viewBox='0 0 800 1107' xmlns='http://www.w3.org/2000/svg'>
      <defs>
        <filter id='dropshadow'>
          <feGaussianBlur in='SourceAlpha' stdDeviation='3'></feGaussianBlur>
          <feOffset dx='2' dy='2' result='offsetblur'></feOffset>
          <feMerge>
            <feMergeNode></feMergeNode>
            <feMergeNode in='SourceGraphic'></feMergeNode>
          </feMerge>
        </filter>
      </defs>
      <g filter='url(#dropshadow)'>
        { Object.keys(paths).map((key, index) => (
          <path key={index}
                id={paths[key].id}
                className={'OUTLINE' + (selectedArea === key ? ' SELECTED' : '')}
                d={paths[key].d}></path>
        ))}

      </g><g filter='url(#dropshadow)'>
      { Object.keys(paths).map((key, index) => (
        <text key={index}
              id={paths[key].id}
              className='TEXT'
              x={paths[key].x}
              y={paths[key].y}>
          {key}
        </text>
      ))}
    </g></svg>
  );
}

